"use client";

import { useLayoutEffect, useRef } from "react";
import gsap from "gsap";
import MagneticButton from "./ui/MagneticButton";

export default function Hero() {
  const containerRef = useRef<HTMLElement>(null);

  useLayoutEffect(() => {
    const ctx = gsap.context(() => {
      const tl = gsap.timeline({ delay: 0.3 });

      // Entrance sequence
      tl.from(".hero-eyebrow", {
        opacity: 0,
        duration: 0.4,
      })
        .from(".hero-headline .word", {
          y: 100,
          opacity: 0,
          stagger: 0.1,
          duration: 0.8,
          ease: "power3.out",
        })
        .from(".hero-headline .highlight", {
          scale: 0,
          duration: 0.6,
          ease: "back.out(1.7)",
        }, "-=0.3")
        .from(".hero-subheadline", {
          y: 30,
          opacity: 0,
          duration: 0.6,
        }, "-=0.2")
        .from(".hero-cta", {
          scale: 0.8,
          opacity: 0,
          stagger: 0.1,
          duration: 0.4,
          ease: "back.out(1.7)",
        }, "-=0.2");

      // Cursor spotlight effect
      const spotlight = document.querySelector<HTMLElement>(".hero-spotlight");
      if (spotlight) {
        const quickToX = gsap.quickTo(spotlight, "--x", {
          duration: 0.6,
          ease: "power3",
        });
        const quickToY = gsap.quickTo(spotlight, "--y", {
          duration: 0.6,
          ease: "power3",
        });

        document.addEventListener("mousemove", (e) => {
          quickToX(`${e.clientX}px`);
          quickToY(`${e.clientY}px`);
        });
      }

      // Continuous animations
      gsap.to(".hero-highlight", {
        opacity: 0.7,
        yoyo: true,
        repeat: -1,
        duration: 3,
        ease: "power1.inOut",
      });

    }, containerRef);

    return () => ctx.revert();
  }, []);

  return (
    <section
      ref={containerRef}
      className="relative min-h-screen w-full flex items-center justify-center overflow-hidden bg-[--obsidian]"
    >
      {/* ANIMATED BACKGROUND */}
      <div className="absolute inset-0 overflow-hidden">
        {/* Gradient Mesh */}
        <div className="absolute inset-0 bg-gradient-to-br from-[#D4FF00]/5 via-transparent to-[#00D9FF]/5 animate-pulse"
          style={{ animationDuration: "8s" }}
        />

        {/* Noise Texture */}
        <div className="absolute inset-0 opacity-20 bg-[url('https://grainy-gradients.vercel.app/noise.svg')] mix-blend-overlay" />

        {/* Cursor Spotlight */}
        <div
          className="hero-spotlight absolute w-[600px] h-[600px] rounded-full pointer-events-none opacity-30"
          style={{
            background: "radial-gradient(circle, rgba(212,255,0,0.2) 0%, transparent 70%)",
            left: "var(--x, 50%)",
            top: "var(--y, 50%)",
            transform: "translate(-50%, -50%)",
          }}
        />
      </div>

      {/* CONTENT */}
      <div className="relative z-10 container-custom text-center max-w-5xl">

        {/* Eyebrow */}
        <div className="hero-eyebrow mb-8">
          <span className="text-[--silver] text-xs uppercase tracking-[0.2em] font-mono-tech">
            AI-First Software Company
          </span>
        </div>

        {/* Main Headline */}
        <h1 className="hero-headline font-display text-[clamp(3.5rem,10vw,10rem)] leading-[0.95] text-[--ivory] mb-12">
          <div className="word">WE BUILD THE</div>
          <div className="word">SOFTWARE THAT</div>
          <div className="word">
            DOESN'T EXIST{" "}
            <span className="hero-highlight highlight text-[--lime]">YET.</span>
          </div>
        </h1>

        {/* Subheadline */}
        <p className="hero-subheadline text-[clamp(1.125rem,2.5vw,1.375rem)] text-[--silver] max-w-[700px] mx-auto leading-[1.7] mb-16">
          Perioxia is a product studio creating AI-powered solutions for emerging problems.
          We don't build better versions of existing tools. We build what comes next.
        </p>

        {/* CTAs */}
        <div className="flex flex-col sm:flex-row gap-6 items-center justify-center">
          <MagneticButton className="hero-cta bg-[--lime] text-[--obsidian] px-10 py-5 text-lg font-bold uppercase tracking-wide hover:shadow-[0_0_40px_rgba(212,255,0,0.4)] transition-shadow">
            View Our Work
          </MagneticButton>

          <MagneticButton className="hero-cta border-2 border-[--lime] text-[--lime] bg-transparent px-10 py-5 text-lg font-bold uppercase tracking-wide hover:bg-[--lime] hover:text-[--obsidian] transition-all">
            Let's Talk
          </MagneticButton>
        </div>

        {/* Scroll Indicator */}
        <div className="absolute bottom-12 left-1/2 -translate-x-1/2 animate-bounce">
          <div className="text-[--silver] text-xs uppercase tracking-[0.2em] font-mono-tech">
            Scroll to Explore
          </div>
        </div>

      </div>
    </section>
  );
}
