"use client";

import { useEffect, useRef } from "react";
import gsap from "gsap";

export default function CustomCursor() {
    const dotRef = useRef<HTMLDivElement>(null);
    const circleRef = useRef<HTMLDivElement>(null);

    useEffect(() => {
        // Only on desktop with fine pointer
        if (window.matchMedia("(pointer: coarse)").matches) return;

        const dot = dotRef.current;
        const circle = circleRef.current;
        if (!dot || !circle) return;

        // Quick animations for smooth following
        const dotQuick = gsap.quickTo(dot, "x", { duration: 0.1, ease: "power3" });
        const dotQuickY = gsap.quickTo(dot, "y", { duration: 0.1, ease: "power3" });

        const circleQuick = gsap.quickTo(circle, "x", { duration: 0.3, ease: "power3" });
        const circleQuickY = gsap.quickTo(circle, "y", { duration: 0.3, ease: "power3" });

        const moveCursor = (e: MouseEvent) => {
            dotQuick(e.clientX);
            dotQuickY(e.clientY);
            circleQuick(e.clientX);
            circleQuickY(e.clientY);
        };

        // State changes for different elements
        const addHoverEffects = () => {
            const interactiveElements = document.querySelectorAll("a, button, [role='button']");

            interactiveElements.forEach((el) => {
                el.addEventListener("mouseenter", () => {
                    gsap.to(dot, { scale: 2, duration: 0.2 });
                    gsap.to(circle, { scale: 0.5, duration: 0.2 });
                });

                el.addEventListener("mouseleave", () => {
                    gsap.to(dot, { scale: 1, duration: 0.2 });
                    gsap.to(circle, { scale: 1, duration: 0.2 });
                });
            });
        };

        document.addEventListener("mousemove", moveCursor);
        addHoverEffects();

        return () => {
            document.removeEventListener("mousemove", moveCursor);
        };
    }, []);

    return (
        <>
            <div ref={dotRef} className="cursor-dot hidden lg:block" />
            <div ref={circleRef} className="cursor-circle hidden lg:block" />
        </>
    );
}
