"use client";

import { useRef, useEffect, ReactNode } from "react";
import gsap from "gsap";

interface MagneticButtonProps {
    children: ReactNode;
    className?: string;
    strength?: number;
}

export default function MagneticButton({
    children,
    className = "",
    strength = 20
}: MagneticButtonProps) {
    const buttonRef = useRef<HTMLButtonElement>(null);
    const innerRef = useRef<HTMLSpanElement>(null);

    useEffect(() => {
        const button = buttonRef.current;
        const inner = innerRef.current;
        if (!button || !inner) return;

        let boundingRect: DOMRect | null = null;

        const handleMouseEnter = () => {
            boundingRect = button.getBoundingClientRect();
        };

        const handleMouseMove = (e: MouseEvent) => {
            if (!boundingRect) return;

            const x = e.clientX - boundingRect.left;
            const y = e.clientY - boundingRect.top;

            const centerX = boundingRect.width / 2;
            const centerY = boundingRect.height / 2;

            const deltaX = (x - centerX) / centerX;
            const deltaY = (y - centerY) / centerY;

            gsap.to(button, {
                x: deltaX * strength,
                y: deltaY * strength,
                duration: 0.3,
                ease: "power2.out",
            });

            // Inner element moves opposite direction (parallax)
            gsap.to(inner, {
                x: -deltaX * (strength * 0.3),
                y: -deltaY * (strength * 0.3),
                duration: 0.3,
                ease: "power2.out",
            });
        };

        const handleMouseLeave = () => {
            gsap.to([button, inner], {
                x: 0,
                y: 0,
                duration: 0.5,
                ease: "elastic.out(1, 0.3)",
            });
            boundingRect = null;
        };

        button.addEventListener("mouseenter", handleMouseEnter);
        button.addEventListener("mousemove", handleMouseMove);
        button.addEventListener("mouseleave", handleMouseLeave);

        return () => {
            button.removeEventListener("mouseenter", handleMouseEnter);
            button.removeEventListener("mousemove", handleMouseMove);
            button.removeEventListener("mouseleave", handleMouseLeave);
        };
    }, [strength]);

    return (
        <button ref={buttonRef} className={`relative ${className}`}>
            <span ref={innerRef} className="block">
                {children}
            </span>
        </button>
    );
}
